.navigation-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: row;

  li {
    padding-right: 30px;
  }
}

.dropdown {
  position: relative;
  display: inline-block;

  .dropdown-button {
    border: 0;
    background-color: white;

    &:hover,
    :focus {
      background-color: $button-color;
      color: white;
      cursor: pointer;
    }
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 160px;
    overflow: auto;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;

    a {
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
    }
  }

  &.show {
    .dropdown-content {
      display: block;
    }

    .dropdown-button {
      background-color: $button-color;
      color: white;
      cursor: pointer;
    }
  }
}
